@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&family=Texturina&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rosarivo&display=swap);
*{margin:0;padding:0}Body{background:#0c0c53;background:linear-gradient(0deg, #103042 0%, #0c0c53 100%);color:white;font-family:'Rosarivo', serif}Body::-webkit-scrollbar{width:10px}Body::-webkit-scrollbar-track{box-shadow:inset 0px 0px 3px black}Body::-webkit-scrollbar-thumb{box-shadow:inset 2px 0px 3px black;background:#00a37d}Body::-webkit-scrollbar-thumb:hover{background:#03dba9}li{list-style:none}h1,h2,h3{font-family:Texturina;letter-spacing:0.1em}a,a:visited,a:hover,a:link,a:active{text-decoration:none;color:white}html{scroll-behavior:smooth}button{border:none;background-color:rgba(0,0,0,0)}

.Page .imgFixed{position:absolute;z-index:-10;opacity:0.5;transition:all 0.5s}.Page #bubbleImg{top:30%}.Page #bubbleImg2{top:150%;right:5%}.Page #bubbleImg3{top:240%;left:5%}.Page #cImg{top:100%}.Page #htmlImg{top:280%;left:1%}.Page #jsImg{top:180%;left:2%}.Page #reactImg{top:200%;right:5%}.Page #unityImg{top:120%;right:2%}.Page #triskel1{top:80%;right:8%;opacity:1}.Page #triskel2{top:150%;left:10%;opacity:1}.Page #triskel3{top:235%;right:10%;opacity:1}.Page #flower1{top:275%;right:17%}.Page #flower2{top:20%}.Page #flower22{top:80%;left:20%}.Page #visualImg{top:215%;left:5%}@media screen and (max-width: 1430px){.Page #bubbleImg3{top:200%;left:5%}.Page #htmlImg{top:230%;left:1%}.Page #triskel3{top:185%;right:10%;opacity:1}.Page #flower1{top:230%;right:17%}.Page #flower2{top:20%}.Page #flower22{top:80%;left:20%}.Page #visualImg{top:190%;left:5%}}@media screen and (max-width: 992px){.Page #bubbleImg{top:35%}.Page #bubbleImg2{top:155%;right:5%}.Page #bubbleImg3{top:255%;left:0%}.Page #cImg{top:110%}.Page #htmlImg{top:310%;left:1%}.Page #jsImg{top:185%;left:2%}.Page #reactImg{top:200%;right:5%}.Page #unityImg{top:130%;right:2%}.Page #triskel1{top:85%;right:0%;opacity:1}.Page #triskel2{top:155%;left:0%;opacity:1}.Page #triskel3{top:235%;right:0%;opacity:1}.Page #flower1{top:310%;right:7%}.Page #flower2{top:20%}.Page #flower22{top:80%;left:20%}.Page #visualImg{top:215%;left:5%}}

.Entete{position:-webkit-sticky;position:sticky;z-index:50;top:0;height:40px;margin:0;padding:10px;font-size:1.2em;background-color:black;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;box-shadow:0px 0px 20px 10px #000000}.Entete p{transition:all 1s}.Entete li:hover p{color:#000;text-shadow:0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #49ff18, 0 0 30px #49FF18, 0 0 40px #49FF18, 0 0 55px #49FF18, 0 0 75px #49ff18}.Entete .navPrincipale{padding:0 0 0 5%;list-style:none;display:-webkit-flex;display:flex;margin:0 auto;font-size:1.2em;font-family:texturina}.Entete .navPrincipale li{cursor:pointer}.Entete .navPrincipale li:not(:last-of-type){padding-right:15px;border-right:1px solid white}.Entete .navPrincipale li:not(:first-of-type){padding-left:15px}.Entete .langue{position:relative;display:-webkit-flex;display:flex;right:30px;font-size:0.9em;z-index:15}.Entete .langue li{list-style:none}.Entete .langue li button{background-color:rgba(0,0,0,0);border:none;color:white;font-family:'Akaya Telivigala', cursive;font-size:1em;cursor:pointer}.Entete .langue li button:focus{outline:0}.Entete .langue li:not(:last-of-type){padding:0 15px;border-right:1px solid white}.Entete .langue li:not(:first-of-type){padding:0 15px}.Entete .langue .lit{border-radius:50%;cursor:default;color:#000;background:#232323;text-shadow:0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #49ff18, 0 0 30px #49FF18, 0 0 40px #49FF18, 0 0 55px #49FF18, 0 0 75px #49ff18}@media screen and (max-width: 700px){.Entete{height:20px}.Entete .navPrincipale{font-size:0.9em;margin:0}.Entete .langue{position:absolute;bottom:4px;right:0;font-size:0.6em}}

h1{position:absolute;-webkit-align-self:center;align-self:center;font-size:3em;text-shadow:0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #49ff18, 0 0 30px #49FF18, 0 0 40px #49FF18, 0 0 55px #49FF18, 0 0 75px #49ff18;color:black;transition:font-size 0.5s}.Banniere{display:-webkit-flex;display:flex;margin:0 auto;-webkit-flex-flow:row wrap;flex-flow:row wrap;-webkit-justify-content:center;justify-content:center}.Banniere .imageBanniere{display:-webkit-flex;display:flex;margin:40px 0;-webkit-justify-content:center;justify-content:center;position:relative;justify-content:center}.Banniere .imageBanniere img{width:80%;box-shadow:0px 0px 20px 10px #000000;transition:all 0.5s}.Banniere .info{margin-bottom:60px;text-align:center}@media screen and (max-width: 1430px){.Banniere .imageBanniere h1{font-size:1.7em}}@media screen and (max-width: 700px){.Banniere .imageBanniere img{width:100%}.Banniere h1{font-size:1.3em !important}}

.ConteneurProjets{margin:50px auto;width:65%;background-color:rgba(180,180,180,0.6);display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;text-align:center;box-shadow:0px 0px 20px 10px #000000;border-radius:5px;transition:all 0.5s}@media screen and (max-width: 1500px){.ConteneurProjets{width:80%}}@media screen and (max-width: 700px){.ConteneurProjets{width:91%}}

.ListeProjets{display:-webkit-flex;display:flex;-webkit-flex-flow:column wrap;flex-flow:column wrap;margin:20px 40px 40px}.ListeProjets h2{margin:20px 0 20px;padding:5px 0 10px;border-bottom:5px solid #00A37D;border-top:5px solid #00A37D;border-radius:20%;background-color:#000}.ListeProjets ul{list-style:none;display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-evenly;justify-content:space-evenly;margin:0;padding:0}@media screen and (max-width: 700px){.ListeProjets h2{font-size:1.1em}}

.Projet{margin:20px;width:400px;height:400px;box-shadow:0px 0px 11px 5px #00a37d;-webkit-align-items:center;align-items:center;border-radius:5px;background-color:#333}.Projet .image{position:-webkit-sticky;position:sticky;z-index:1;width:400px;height:300px;overflow:hidden;cursor:pointer}.Projet .image .hoverImg{position:absolute;width:100%;height:100%;background-color:black;opacity:0;transition:opacity 0.5s;border-radius:5px 5px 0 0}.Projet .image img{width:400px;height:300px;object-fit:cover;background-color:#14213D;border-radius:5px 5px 0 0}.Projet .image .contribution{position:relative;padding:15px;text-align:center;transition:all 0.5s;background-color:rgba(0,0,0,0.8);font-size:1.3em;color:white;font-family:'Rosarivo', serif}.Projet .image:hover .contribution{-webkit-transform:translateY(-100%);transform:translateY(-100%)}.Projet .image:hover .hoverImg{opacity:0.5}.Projet .info{height:25%;display:-webkit-flex;display:flex;-webkit-flex-flow:column wrap;flex-flow:column wrap;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.Projet .noPointer{cursor:default}@media screen and (max-width: 1430px){.Projet{width:325px;height:325px;margin:14px}.Projet .image{width:325px;height:225px}.Projet .image img{width:325px;height:225px}}@media screen and (max-width: 700px){.Projet{width:300px;height:300px}.Projet h3{font-size:1em}.Projet .image{width:300px;height:200px}.Projet .image img{width:300px;height:200px}}

.Footer{margin:0 auto;height:40px;width:50%;margin-top:50px;border-radius:100px 100px 0 0;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around;background:#00a37d;background:linear-gradient(0deg, #00a37d 0%, #14213d 100%);box-shadow:0px 0px 10px 5px #000000}.Footer span{margin:0;padding:0;height:21px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;cursor:pointer;transition:all 0.5s}.Footer span:first-child{border-radius:50%}.Footer span:hover{-webkit-transform:scale(1.3);transform:scale(1.3);box-shadow:0px 0px 15px 3px #000000}

#Lightbox{position:fixed;z-index:1000;height:100vh;width:100vw;background-color:rgba(0,0,0,0.7);display:-webkit-flex;display:flex;-webkit-flex-flow:column nowrap;flex-flow:column nowrap;transition:top 1s cubic-bezier(0.175, 0.885, 0.32, 1);font-size:1.2em;overflow-y:scroll}#Lightbox .contenuLightbox{position:relative;top:100px}#Lightbox .contenuLightbox button{position:relative;display:-webkit-flex;display:flex;top:10px;right:-99%;height:30px;width:30px;border-radius:15px;background:#00a37d;font-weight:bold;font-size:1.5em;transition:all 0.5s;box-shadow:0px 0px 11px 5px #00a37d;-webkit-justify-content:center;justify-content:center}#Lightbox .contenuLightbox button .closeIcon{-webkit-align-self:center;align-self:center}#Lightbox .contenuLightbox button:hover{background-color:#007da3;color:#8d0000}#Lightbox .contenuLightbox button:focus{outline:0}#Lightbox .contenuLightbox .imageComposant{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:50vw;margin:0 auto;border-radius:5px}#Lightbox .contenuLightbox .imageComposant img{border-radius:5px;height:50vh;width:50vw;object-fit:cover;box-shadow:0px 0px 20px 10px #000000}#Lightbox .contenuLightbox .imageComposant h3{text-align:center;margin-bottom:30px;color:black;text-shadow:0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #49ff18, 0 0 30px #49FF18, 0 0 40px #49FF18, 0 0 55px #49FF18, 0 0 75px #49ff18}#Lightbox .contenuLightbox .descriptLightbox{width:40%;background-color:black;padding:2%;border-radius:5px;box-shadow:0px 0px 20px 10px #000000;margin:0 auto}#Lightbox .contenuLightbox .descriptLightbox .descript{margin:0 auto;text-align:justify;width:80%}#Lightbox .contenuLightbox .descriptLightbox a{text-align:center;display:block;margin-top:15px;transition:all 0.5s;color:#00a37d}#Lightbox .contenuLightbox .descriptLightbox a:hover{-webkit-transform:scale(1.2);transform:scale(1.2);color:black;text-shadow:0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #49ff18, 0 0 30px #49FF18, 0 0 40px #49FF18, 0 0 55px #49FF18, 0 0 75px #49ff18}.LightboxInactive{top:-130%}.LightboxActive{top:0%}@media screen and (max-width: 1430px){#Lightbox .contenuLightbox button{top:10px;right:-98%}#Lightbox .contenuLightbox .imageComposant{width:60vw}#Lightbox .contenuLightbox .imageComposant #lightboxImg{height:50vh;width:60vw}}@media screen and (max-width: 700px){#Lightbox .contenuLightbox button{top:15px;right:-97%}#Lightbox .contenuLightbox .imageComposant{width:70vw}#Lightbox .contenuLightbox .imageComposant #lightboxImg{height:40vh;width:70vw}#Lightbox .contenuLightbox .descriptLightbox{width:70%}}

